@charset "UTF-8";
/** 常用色值 **/
/** 常用字体大小 **/
.header {
  background-color: #f1f3f5; }
  .header .header-top {
    height: calc(5vh);
    line-height: 20px;
    padding: 0 20px;
    text-align: right; }
    .header .header-top .logo {
      line-height: 20px;
      text-align: left;
      font-size: 18px; }
      .header .header-top .logo img {
        height: 20px;
        vertical-align: middle; }
      .header .header-top .logo span {
        margin-left: 5px; }
    .header .header-top a {
      margin-left: 20px; }
  .header .breadcrumb {
    height: 40px;
    line-height: 40px;
    text-align: right;
    padding: 0 20px;
    border-top: 1px solid #f9c700; }
    .header .breadcrumb .breadcrumb-title {
      text-align: center;
      font-size: 18px; }
      .header .breadcrumb .breadcrumb-title:after {
        position: absolute;
        content: '';
        left: 83px;
        top: 39px;
        border-top: 9px solid #fff;
        border-left: 12px solid transparent;
        border-right: 12px solid transparent; }
    .header .breadcrumb .weather {
      text-align: right;
      font-size: 14px; }
      .header .breadcrumb .weather .date {
        margin-right: 10px;
        vertical-align: middle; }
      .header .breadcrumb .weather .weather-img img {
        height: 15px; }
      .header .breadcrumb .weather .weather-detail {
        margin-left: 5px;
        vertical-align: middle; }

.simple-page .header-top {
  background: #1890ff;
  color: #fff; }
